.c {
    /* height: 100vh; */
    position: relative;
}

.c-bg {
    width: 20px;
    height: 100%;
    background-color: #59b256;
    position: absolute;
}

.c-wrapper {
    padding: 50px;
    display: flex;
}

.c-left {
    flex: 1;
    display: flex;
    justify-content: center;
    flex-direction: column;
}


.c-right {
    flex: 1;
}

.c-title {
    font-size: 60px;
    width: 80%;
}

.c-info-item {
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 70%;

}

.c-icon {
    width: 30px;
    height: 30px;
    margin-right: 2px;
    color: black;
}

.c-download-icon {
    width: 30px;
    height: 30px;
    margin-right: 2px;
    margin-left: 20px;
    color: black;
}

.c-desc {
    font-weight: 600;
    font-size: 20px;
}

.form {
    margin-top: 20px;

}



.textarea {
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;

    border: 2px solid black;
}



button {
    border: none;
    padding: 15px;
    background-color: #59b256;
    color: white;
    font-weight: 500;
}

input {
    color: black;
}

.c-input {

    width: 50%;
    height: 50px;
    border: none;
    border: 2px solid black;

    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
    display: block;

}

.icon-link {
    padding-left: 50px;
}





@media screen and (max-width: 480px) {
    .c-wrapper {
        flex-direction: column;

    }

    .c-input {
        width: 100%;
    }

    .textarea {
        width: 100%;
        margin: 0px;

    }

    button {
        margin-top: 10px;
    }

    .icon-link {
        justify-content: center;
        padding-left: 50px;
    }


}