@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App{font-family:"Ubuntu",sans-serif;background:linear-gradient(135deg, #62f4de 0%, #707aff 100%);color:#000}@media(prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:#fff}
.h{padding:10px}.hdr{display:flex;flex-direction:row;padding-left:30px}
.icon{font-size:75px;padding:10px;color:#fff}.ftr{position:relative;color:#fff}p{font-size:20px;padding:10px;display:flex}
.i{display:flex;height:100vh}.i-left{flex:1 1;align-items:center;justify-content:center}.i-right{flex:1 1;position:relative}.i-left-wrapper{padding:50px;height:50%;display:flex;flex-direction:column;justify-content:space-between}.i-intro{font-size:30px;font-weight:300}.i-name{font-size:60px}.i-img{width:100%;height:100%;object-fit:cover;position:absolute}.i-bg{-webkit-clip-path:circle(35% at 50% 45%);clip-path:circle(35% at 50% 45%);width:100%;height:100%;position:absolute}@media screen and (max-width: 768px){.i{flex-direction:column}.i-left{display:flex}.i-left-wrapper{padding:10px;align-items:center;text-align:center;height:100%;justify-content:center}.i-right{display:flex;justify-content:center}.i-bg{-webkit-clip-path:polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);clip-path:polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);height:250%;width:70%}.i-img{width:100%;height:120%;object-fit:cover}}
.fade-in-section{opacity:0;-webkit-transform:translateY(20vh);transform:translateY(20vh);visibility:hidden;transition:opacity .2s ease-out,-webkit-transform .6s ease-out;transition:opacity .2s ease-out,transform .6s ease-out;transition:opacity .2s ease-out,transform .6s ease-out,-webkit-transform .6s ease-out;will-change:opacity,visibility}.fade-in-section.is-visible{opacity:1;-webkit-transform:none;transform:none;visibility:visible}
.pl{padding-top:150px}.pl-title{font-weight:600;font-size:50px;position:relative;text-align:center}.pl-desc{text-align:center;padding-bottom:10px;justify-content:center}.pl-table{text-align:center}.pl-row{justify-content:space-between;align-items:center;padding-bottom:120px}@media screen and (max-width: 768px){.pl-title{padding:auto}}
.cr{position:relative;padding-bottom:150px}.brdr{color:#000}.gi{color:#000;font-size:50px}.underline{text-decoration:underline;text-underline-offset:5px}
.c {
    /* height: 100vh; */
    position: relative;
}

.c-bg {
    width: 20px;
    height: 100%;
    background-color: #59b256;
    position: absolute;
}

.c-wrapper {
    padding: 50px;
    display: flex;
}

.c-left {
    flex: 1 1;
    display: flex;
    justify-content: center;
    flex-direction: column;
}


.c-right {
    flex: 1 1;
}

.c-title {
    font-size: 60px;
    width: 80%;
}

.c-info-item {
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 70%;

}

.c-icon {
    width: 30px;
    height: 30px;
    margin-right: 2px;
    color: black;
}

.c-download-icon {
    width: 30px;
    height: 30px;
    margin-right: 2px;
    margin-left: 20px;
    color: black;
}

.c-desc {
    font-weight: 600;
    font-size: 20px;
}

.form {
    margin-top: 20px;

}



.textarea {
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;

    border: 2px solid black;
}



button {
    border: none;
    padding: 15px;
    background-color: #59b256;
    color: white;
    font-weight: 500;
}

input {
    color: black;
}

.c-input {

    width: 50%;
    height: 50px;
    border: none;
    border: 2px solid black;

    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
    display: block;

}

.icon-link {
    padding-left: 50px;
}





@media screen and (max-width: 480px) {
    .c-wrapper {
        flex-direction: column;

    }

    .c-input {
        width: 100%;
    }

    .textarea {
        width: 100%;
        margin: 0px;

    }

    button {
        margin-top: 10px;
    }

    .icon-link {
        justify-content: center;
        padding-left: 50px;
    }


}
.t{padding-top:50px}.tech{position:relative}.tech-title{font-weight:600;font-size:50px;position:relative;text-align:center;padding-bottom:50px}.tech-table{text-align:center}.tech-row{justify-content:space-between;align-items:center}@media screen and (max-width: 768px){.tech-title{margin-top:100px}}
.tech-card{position:relative;padding-bottom:150px}.tech-card-title{color:#000}.t-icon{padding:10px}@media screen and (max-width: 768px){.tech-card{padding-bottom:50px}}
